import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { map } from 'rxjs/operators';
import { AuthService } from './auth.service';
import { MatSnackBar } from '@angular/material/snack-bar';
import { PayModel } from '../model/payform.model';

export type bankNameInterface = string[];

@Injectable({ providedIn: 'root' })
export class DataStorageService {
  payFormData: PayModel[];
  bankName = [];
  deletePaymentId: string = null;

  constructor(
    private authService: AuthService,
    private http: HttpClient,
    private _snackBar: MatSnackBar
  ) {}
  // payment call backs
  storePayData(payData: PayModel) {
    let userToken = this.authService.token;
    return this.http.post(
      'https://nse-app-85c76-default-rtdb.firebaseio.com/payment.json?auth=' +
        userToken,
      payData
    );
  }
  fetchPayData() {
    let userToken = this.authService.token;
    return this.http
      .get<PayModel[]>(
        'https://nse-app-85c76-default-rtdb.firebaseio.com/payment.json?auth=' +
          userToken
      )
      .pipe(
        map((responseData) => {
          this.payFormData = [];
          for (let k in responseData) {
            responseData[k].delId = k;
            this.payFormData.push(responseData[k]);
          }
        })
      );
  }
  deletePayData() {
    let delId = this.deletePaymentId;
    let userToken = this.authService.token;
    return this.http.delete(
      `https://nse-app-85c76-default-rtdb.firebaseio.com/payment/${delId}.json?auth=${userToken}`
    );
    // .delete('https://nse-app-85c76-default-rtdb.firebaseio.com/payment.json?auth=' + userToken)
  }
  // dashboard call backs
  storeBankName(bankNameArr: string[]) {
    let userToken = this.authService.token;
    return this.http.put(
      'https://nse-app-85c76-default-rtdb.firebaseio.com/dashboard/bankName.json?auth=' +
        userToken,
      bankNameArr
    );
  }
  fetchBankName() {
    let userToken = this.authService.token;

    console.log('fetchBankName');

    return this.http
      .get<bankNameInterface>(
        'https://nse-app-85c76-default-rtdb.firebaseio.com/dashboard/bankName.json?auth=' +
          userToken
      )
      .pipe(
        map((responseData) => {
          this.bankName = responseData;
        })
      );
  }
  openSnackBar(message: string, action: string) {
    this._snackBar.open(message, action, {
      duration: 2000,
    });
  }
}
